body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  overflow-x: auto;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th,
td {
  text-align: left;
  padding: 0.5rem;
  border: 1px solid #ddd;
}

th {
  cursor: pointer;
  background-color: #f2f2f2;
  user-select: none;
}

th:hover {
  background-color: #e6e6e6;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

button {
  background-color: #4CAF50;
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #3e8e41;
}
input[type="text"] {
  width: 98%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 20px;
}

h2 {
  margin-bottom: 1rem;
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
  font-size: 1.7rem;
  display: inline-block;
  width: 97%;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

button.active {
  background-color: #3e8e41;
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }

  table {
    font-size: 0.9rem;
  }

  th,
  td {
    padding: 0.3rem;
  }

  button {
    padding: 0.3rem 0.7rem;
  }
}

/* Add these styles to your App.css or your preferred stylesheet */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.pagination a {
  display: inline-block;
  padding: 8px 16px;
  margin: 0 5px;
  text-decoration: none;
  background-color: #f1f1f1;
  color: #333;
  border-radius: 4px;
  cursor: pointer;
}

.pagination a:hover {
  background-color: #ddd;
}

.pagination a.active {
  background-color: #007bff;
  color: #fff;
}

.pagination a.disabled {
  pointer-events: none;
  background-color: #ccc;
  color: #666;
}
